*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

button {
    width: 240px;
    height: 40px;
    color: rgb(249, 249, 249);
    background: rgb(34,171,198);
    background: linear-gradient(90deg, rgba(34,171,198,1) 0%, rgba(9,9,121,1) 50%, rgba(34,171,198,1) 100%);
    border: 0;
    border-radius: 20px;
}

body {
    padding: 10px 10px 0;
}